// Media objects
// Source: http://stubbornella.org/content/?p=497
// --------------------------------------------------


// Common styles
// -------------------------

// Clear the floats
.media,
.media-body {
  overflow: hidden;
  zoom: 1;
}

// Proper spacing between instances of .media
.media,
.media .media {
  margin-top: 15px;
}
.media:first-child {
  margin-top: 0;
}

// For images and videos, set to block
.media-object {
  display: block;
}

// Reset margins on headings for tighter default spacing
.media-heading {
  margin: 0 0 5px;
}


// Media image alignment
// -------------------------

.media {
  > .pull-left {
    margin-right: 10px;
  }
  > .pull-right {
    margin-left: 10px;
  }
}


// Media list variation
// -------------------------

// Undo default ul/ol styles
.media-list {
  padding-left: 0;
  list-style: none;
}


.appcard {
	@width:		258px;
	@height:	300px;
	@padding:	16px;
	@margin:	4px;
	@radius:	2px;
	
	border-radius: 		@radius;
	width:				@width;
	height:				@height;
	border:				1px solid #CBCBCB;
	background-color:	#ffffff;
	display:			inline-block;
	margin:				@margin;
	
	&.flat {
		border:		0;
		margin:		0 25px 25px 0px;
	}
	
	& > img {
		width:				100%;
		border-radius: 		@radius;
	}
	& > div.title {
		padding:		@padding @padding 4px @padding;
		color:			#414A51;
		font-size:		16px;
		font-weight:	bold;
		& > div.rating {
			float:	right;
		}
	}
	& > div.description {
		padding:		0 @padding;
		color:			#919CA4;
		font-size:		13px;
		font-weight:	regular;
		
	}
	
	&:hover {
		//box-shadow:					0 0 6px rgba(1,130,187,0.7);
		//border-color:				rgba(1,130,187,0.9)
		/*-moz-box-shadow:			10px 10px 5px #000000;
		-webkit-box-shadow:			10px 10px 5px #000000;*/
	}
	
	&.with-actions {
		& > .preview {
			border-radius: 		@radius;
			position:			relative;
			height:				194px;
			
			& > img {
				width:			100%;
				height:			194px;
				border-radius: 		@radius;
			}
			
			& > .img {
				background-size:   		cover;
				background-repeat: 		no-repeat;
				background-position: 	center center;
				width:					100%;
				height:					194px;
			}
			
			& > .overlay {
				display:			none;
				background-color:	rgba(142,109,155,0.7);
				position:			absolute;
				top:				0;
				height:				194px;
				width:				100%;
				& > .actions {
					margin:			82px auto;
					text-align:		center;
					
					& > .action {
						text-decoration:	none;
						cursor:				pointer;
						line-height:		30px;
						padding:			0 10px;
						background-color:	transparent;
						border:				3px solid #FFFFFF;
						color:				#FFFFFF;
						font-size:			14px;
						font-weight:		bold;
						display:			inline-block;
						
						&:hover {
							background-color:	#FFFFFF;
							color:				#404A51;
						}
					}
				}
			}
		}
		&:hover {
			& .overlay {
				display:	block;
			}
		}
	}
}


.rating {
	
	// Image is a tile (http://i.imgur.com/SChwZbL.png) containing the full, half and empty starts.
	// Display is controlled using 'background-position'
	// Current image has no alpha transparency, and need to be re-encoded
	@image:			url('data:image/png;base64,');
	@tileWidth:		15px;
	@tileHeight:	14px;
	
	
	& > .star {
		display:				table-cell;
		background-image:		@image;
		background-repeat:		no-repeat;
		background-position:	(0 - @tileWidth * 2) 0;
		width:					@tileWidth;
		height:					@tileHeight;
		
		&.half {
			background-position:	(0 - @tileWidth) 0;
		}
		
		&.full {
			background-position:	0 0;
		}
	}
}